import { useEffect, useState } from 'react'

export const ProjectListScreen = () => {
  const [list, setList] = useState([])
  useEffect(() => {
    fetch('http://localhost:3001/projects').then(async (res) => {
      if (res.ok) {
        const data = await res.json()
        setList(data)
        // console.log('====================================')
        // console.log(data)
        // console.log('====================================')
      }
    })
  }, [])
  // console.log(list)
  return (
    <div>
      {list.map((item, key) => {
        return <span>{item.name}</span>
      })}
    </div>
  )
}
